import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { sportsListUrl } from "../../../assets/helper/constants";
import { isAuth } from "../../../assets/helper/auth";
import { commonGetRequest } from "../../../redux/features/Auth";

import Default from "./Default";

export default function Slots() {
  const dispatch = useDispatch();
  const { sports_list } = useSelector((state) => state.auth);
  const [complexId, setComplexId] = React.useState("");
  const [stage, setStage] = React.useState(0);
  const [userInput, setUserInput] = React.useState({});

  const loadInitialValues = () => {
    setComplexId(isAuth().vendor_complex_id);
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  const changeState = (data) => {
    if (data.sports && data.date && data.confirm) {
      setUserInput(data);
      setStage(2);
    } else if (data.sports && data.date) {
      setUserInput(data);
      setStage(1);
    } else {
      setUserInput({});
      setStage(0);
    }
  };

  return (
    <>
      <Default
        sportData={sports_list}
        vendor_complex_id={complexId}
        changeState={(data) => changeState(data)}
      />
    </>
  );
}
