import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import { Typography } from "@material-ui/core";
import Divider from "@material-ui/core/Divider";
import classNames from "classnames";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

import Select from "../common/Select";

const numberData = [
  { id: 1, name: "+91" },
  { id: 2, name: "+92" },
];

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  divider: {
    marginLeft: "50%",
    color: "#CCCCCC",
  },
  divider2: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    color: "#CCCCCC",
  },
  typo1: {
    color: "#484848",
    fontWeight: "bold",
  },
  typo2: {
    color: "#484848",
    fontWeight: "500",
  },
  typo3: {
    paddingTop: theme.spacing(3),
    color: "#484848",
    fontWeight: "400",
  },
  textAlignR: {
    textAlign: "right",
  },
  paddingLeft: {
    paddingLeft: theme.spacing(3),
  },
  grid1: {
    display: "flex",
    paddingTop: theme.spacing(2),
  },
  grid2: {
    marginLeft: "auto",
  },
  grid3: {
    marginRight: "auto",
  },
  textField: {
    width: "100%",
  },
  button: {
    backgroundColor: "#272727",
    color: "#fff",
    width: "50%",
    marginLeft: "50%",
  },
  button2: {
      marginRight: theme.spacing(1),
      padding: theme.spacing(1),
      borderRadius: 50,
      minWidth: "150px",
      borderColor: "#040F33",
      textTransform: 'none',
  },
  button3: {
    marginLeft: theme.spacing(1),
    padding: theme.spacing(1),
    borderRadius: 50,
    minWidth: "150px",
    backgroundColor: "#41BEAE",
    textTransform: 'none',
    color: "#fff",
}
}));

const SetFormData = (data) => {
  console.log(data);
};

const Checkout = () => {
  const classes = useStyles();
  return (
    <Paper className={classes.paper}>
      <Grid>
        <Grid container lg={12}>
          <Grid lg={4} sm={12}>
            <Typography variant="h6">TURF BOOKING</Typography>
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Football 5-aslide</Typography>
                <Typography className={classes.typo2}>Tue, 20-Oct</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Turf 2
                </Typography>
                <Typography className={classes.typo2}>6:30am to 7:00am</Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Typography variant="h6">CUSTOMER DETAILS</Typography>
            <Grid>
              <Typography className={classes.typo3}>CONTACT NUMBER</Typography>
            </Grid>
            <Grid className={classes.grid1}>
              <Grid lg={4}>
                {" "}
                <Select
                  data={numberData}
                  regularSelect={1}
                  changeFormData={(newData) => SetFormData(newData)}
                />
              </Grid>
              <Grid lg={8}>
                <TextField
                  className={classes.textField}
                  id="number"
                  label="Number"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  type="input"
                />
              </Grid>
            </Grid>
            <Grid>
              <Typography className={classes.typo3}>FULL NAME</Typography>
            </Grid>
            <Grid className={classes.grid1}>
              {" "}
              <TextField
                className={classes.textField}
                id="name"
                label="Enter your name"
                defaultValue=""
                variant="outlined"
                size="small"
                type="input"
              />
            </Grid>
            <Grid>
              <Typography className={classes.typo3}>Email</Typography>
            </Grid>
            <Grid className={classes.grid1}>
              {" "}
              <TextField
                className={classes.textField}
                id="email"
                label="Enter your EMAIL"
                defaultValue=""
                variant="outlined"
                size="small"
                type="input"
              />
            </Grid>
            <Grid>
              <Typography className={classes.typo3}>BOOKING REMARKS</Typography>
            </Grid>
            <Grid className={classes.grid1}>
              {" "}
              <TextField
                className={classes.textField}
                id="remark"
                label="Remarks"
                multiline
                rows={5}
                defaultValue=""
                variant="outlined"
                size="small"
                type="input"
              />
            </Grid>
          </Grid>
          <Grid lg={1} sm={0}>
            <Divider className={classes.divider} orientation="vertical" />
          </Grid>
          <Grid className={classes.paddingLeft} lg={4} sm={12}>
            <Typography variant="h6">BOOKING DETAILS</Typography>
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Turf Price</Typography>
                <Typography className={classes.typo2}>Extras</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs 600.00
                </Typography>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs 0.00
                </Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Gross</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs 600.00
                </Typography>
              </Grid>
            </Grid>
            <Grid className={classes.grid1}>
              <Grid lg={3}>
                {" "}
                <Select
                  data={numberData}
                  regularSelect={1}
                  changeFormData={(newData) => SetFormData(newData)}
                />
              </Grid>
              <Grid lg={3}>
                <TextField
                  className={classes.textField}
                  id="discount"
                  label="Enter %"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  type="input"
                />
              </Grid>
              <Grid lg={6}>
                <Button className={classes.button} variant="contained">
                  APPLY
                </Button>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>Net Price</Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  Rs 600.00
                </Typography>
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Typography variant="h6">PAYMENT DETAILS</Typography>
            <Grid className={classes.grid1}>
              <Grid lg={3}>
                <Typography className={classes.typo2}>Advance</Typography>
              </Grid>

              <Grid lg={6}>
                <TextField
                  className={classes.textField}
                  id="advance"
                  label="Rs 0"
                  defaultValue=""
                  variant="outlined"
                  size="small"
                  type="input"
                />
              </Grid>

              <Grid lg={3}>
                {" "}
                <Select
                  data={numberData}
                  regularSelect={1}
                  changeFormData={(newData) => SetFormData(newData)}
                />
              </Grid>
            </Grid>
            <Divider className={classes.divider2} />
            <Grid className={classes.grid1}>
              <Grid>
                <Typography className={classes.typo2}>
                  <b>Total Price</b>
                </Typography>
              </Grid>
              <Grid className={classes.grid2}>
                <Typography className={classNames(classes.typo2, classes.textAlignR)}>
                  <b>Rs 600.00</b>
                </Typography>
              </Grid>
            </Grid>
            <Grid className={classes.grid1}>
              <Grid className={classes.grid2}>
                <Button className={classes.button2} variant="outlined">Dismiss</Button>
              </Grid>
              <Grid className={classes.grid3}>
                <Button className={classes.button3} variant="contained">Book Now</Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </Paper>
  );
};

export default Checkout;
