import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { commonGetRequest } from "../../../redux/features/Auth";
import Header from "./Header";
import List from "./List";

import { offerList } from "../../../assets/helper/constants";

const Offer = () => {
  const dispatch = useDispatch();
  const { offer_data } = useSelector((state) => state.auth);
  console.log("offer_data :>> ", offer_data);
  const loadInitialValues = () => {
    dispatch(
      commonGetRequest({
        url: offerList,
        key: "offer_data",
      })
    );
  };

  React.useEffect(() => {
    loadInitialValues();
  }, []);

  return (
    <div>
      <Header />
      <List data={offer_data} />
    </div>
  );
};

export default Offer;
