import {
  Button,
  Divider,
  Grid,
  IconButton,
  makeStyles,
  Paper,
  TextField,
  Typography,
} from "@material-ui/core";
import React from "react";
import { Form, Field } from "react-final-form";
import { FieldArray } from "react-final-form-arrays";
import arrayMutators from "final-form-arrays";
import { useDispatch, useSelector } from "react-redux";
import {
  complexListUrl,
  sportsListUrl,
  generateSnackData,
  complexAddUrl,
  weekData,
} from "../../assets/helper/constants";
import { commonGetRequest } from "../../redux/features/Auth";
import SelectPricing from "../common/SelectPricing";
import { setSnackBar, setIsLoading, setSteps } from "../../redux/features/Auth";
import Server from "../../Server";
import ControlPointIcon from "@material-ui/icons/ControlPoint";
import { useHistory } from "react-router";
import Submit from "../common/Button/BlueContainedRounded";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
    backgroundColor: "#F5F5F5",
    borderColor: "#fff",
    display: "flex",
  },
  paper2: {
    padding: theme.spacing(2),
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    backgroundColor: "#F3F3F3",
  },
  typo1: {
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo2: {
    paddingBottom: theme.spacing(1),
    color: "#484848",
    verticalAlign: "middle",
    fontWeight: "bold",
  },
  typo3: {
    paddingBottom: theme.spacing(1),
    color: "#808080",
    verticalAlign: "middle",
  },
  leftMargin: {
    marginLeft: theme.spacing(3),
    marginTop: theme.spacing(2),
  },
  divider: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
  grid1: {
    display: "flex",
  },
  right: {
    marginLeft: "auto",
  },
  icon: {
    color: "#272727",
    marginLeft: "auto",
  },
  textfield: {
    backgroundColor: "#fff",
    width: "95%",
    marginBottom: theme.spacing(1),
  },
  paddingLeft: {
    paddingLeft: theme.spacing(3),
  },
  button: {
    borderRadius: 50,
    marginTop: theme.spacing(2),
    backgroundColor: theme.palette.base1.main,
    color: "#fff",
    "&:hover": {
      backgroundColor: theme.palette.base1.dark,
    },
  },
  paddingTop: {
    paddingTop: theme.spacing(1),
  },
}));
const AddEditPricingForm = () => {
  const history = useHistory();
  const classes = useStyles();
  const required = (value) => (value ? undefined : "* Required");
  const { sports_list, complex_list, isLoading, steps } = useSelector(
    (state) => state.auth
  );
  let [globalForm, setGlobalForm] = React.useState({});

  React.useEffect(() => {
    setGlobalForm({
      sports_id: "",
      complex_id: complex_list.complex_id,
      from_day: "",
      to_day: "",
      slots: [
        {
          start_time: "",
          end_time: "",
          hourly_pricing: "",
          slot_extension_pricing: "",
          volume_discount: "",
          discount: "",
        },
      ],
    });
  }, []);

  const onSubmit = async (values) => {
    console.log(values);
    //   dispatch(setIsLoading());
    try {
      let res = {};
      res = await Server.post(complexAddUrl, values);

      if (res.data && res.data.status) {
        dispatch(
          setSnackBar(generateSnackData(true, res.data.message, "success"))
        );
      } else {
        dispatch(
          setSnackBar(generateSnackData(true, res.data.message, "error"))
        );
      }
      // dispatch(setIsLoading());
    } catch (error) {
      const resp =
        error.response && error.response.data.message
          ? error.response.data.message
          : "Couldn't connect to our servers.";

      dispatch(setSnackBar(generateSnackData(true, resp, "error")));
      // dispatch(setIsLoading());
    }
  };

  const dispatch = useDispatch();
  React.useEffect(() => {
    dispatch(
      commonGetRequest({
        url: sportsListUrl,
        key: "sports_list",
      })
    );
    dispatch(
      commonGetRequest({
        url: complexListUrl,
        key: "complex_list",
        key_name: "complex_name",
      })
    );
  }, []);
  const package_select_turf = ({ input, meta }) => {
    return (
      <TextField
        id="playground_name"
        label="Enter Playground Name"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const min_player_limit = ({ input, meta }) => {
    return (
      <TextField
        id="min_player_limit"
        label="Enter Minimum Player Limit"
        type="number"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const max_player_limit = ({ input, meta }) => {
    return (
      <TextField
        id="max_player_limit"
        label="Enter Maximum Player Limit"
        type="number"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const weekend_discount = ({ input, meta }) => {
    return (
      <TextField
        id="weekend_discount"
        label="Enter Weekend Discount"
        type="number"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const holiday_discount = ({ input, meta }) => {
    return (
      <TextField
        id="holiday_discount"
        label="Enter Holiday Discount"
        type="number"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_select_sport = ({ input, meta }) => {
    return (
      <SelectPricing
        data={sports_list}
        funct="select"
        title="Select Sport"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_select_from_day = ({ input, meta }) => {
    return (
      <SelectPricing
        data={weekData}
        funct="from"
        title="From"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_select_to_day = ({ input, meta }) => {
    return (
      <SelectPricing
        data={weekData}
        funct="to"
        title="To"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
  };
  const package_slot_start_time = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="from_time"
        label="6:00 PM"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_end_time = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="to_time"
        label="6:00 PM"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_hourly_price = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="hourly_price"
        label="Hourly Price"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_extension_pricing = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="total_amount"
        label="Total Pricing"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_volume_discount = ({ input, meta }) => {
    return (
      <TextField
        className={classes.textfield}
        id="discount_amount"
        label="Discount Price"
        type="input"
        variant="outlined"
        onChange={(event, value) => {
          input.onChange(event.target.value);
        }}
        error={meta && meta.touched && meta.error ? true : false}
        helperText={meta && meta.touched && meta.error ? meta.error : ""}
        value={input?.value || input?.value === 0 ? input?.value : ""}
      />
    );
  };
  const package_slot_discount = ({ input, meta }) => {
    return (
      <SelectPricing
        data={sports_list}
        funct="select"
        input={input}
        meta={meta}
        returnValue={(value) => console.log(value, input)}
      />
    );
    // return (
    //   <TextField
    //     className={classes.textfield}
    //     id="sports_id"
    //     label="Sport"
    //     type="input"
    //     variant="outlined"
    //     onChange={(event, value) => {
    //       input.onChange(event.target.value);
    //     }}
    //     error={meta && meta.touched && meta.error ? true : false}
    //     helperText={meta && meta.touched && meta.error ? meta.error : ""}
    //     value={input?.value || input?.value === 0 ? input?.value : ""}
    //   />
    // );
  };
  return (
    <>
      {!isLoading ? (
        <Form
          initialValues={globalForm}
          onSubmit={onSubmit}
          mutators={{
            ...arrayMutators,
          }}
          render={({
            handleSubmit,
            submitting,
            values,
            form: {
              mutators: { push, pop },
            },
          }) => (
            <form onSubmit={handleSubmit}>
              {globalForm ? (
                <input type="hidden" name="id" value={globalForm.id} />
              ) : null}
              <Grid>
                <Grid>
                  <Paper className={classes.paper} variant="outlined">
                    <Typography variant="h6" className={classes.typo1}>
                      Add Complex
                    </Typography>
                  </Paper>
                </Grid>
                <Divider className={classes.divider} />
                <Grid className={classes.paddingLeft}>
                  <Field
                    className={classes.textField}
                    name="sports_id"
                    component={package_select_sport}
                    validate={required}
                  />
                </Grid>
                <Grid
                  container
                  lg={6}
                  md={12}
                  className={(classes.grid1, classes.paddingLeft)}
                >
                  <Grid className={classes.paddingTop}>
                    <Field
                      className={classes.textField}
                      name="playground_name"
                      component={package_select_turf}
                      validate={required}
                    />
                  </Grid>
                  <Grid className={classes.paddingTop}>
                    <Field
                      className={classes.textField}
                      name="min_player_limit"
                      component={min_player_limit}
                      validate={required}
                    />
                  </Grid>
                  <Grid className={classes.paddingTop}>
                    <Field
                      className={classes.textField}
                      name="max_player_limit"
                      component={max_player_limit}
                      validate={required}
                    />
                  </Grid>
                  <Grid className={classes.paddingTop}>
                    <Field
                      className={classes.textField}
                      name="weekend_discount"
                      component={weekend_discount}
                      validate={required}
                    />
                  </Grid>
                  <Grid className={classes.paddingTop}>
                    <Field
                      className={classes.textField}
                      name="holiday_discount"
                      component={holiday_discount}
                      validate={required}
                    />
                  </Grid>
                </Grid>
                <Grid
                  lg={6}
                  md={12}
                  className={(classes.grid1, classes.paddingLeft)}
                >
                  <Grid>
                    <Field
                      className={classes.textField}
                      name="from_day"
                      component={package_select_from_day}
                      validate={required}
                    />
                    <Field
                      className={classes.textField}
                      name="to_day"
                      component={package_select_to_day}
                      validate={required}
                    />
                  </Grid>

                  <Grid className={classes.right}>
                    <IconButton
                      onClick={() => push("slots", undefined)}
                      className={classes.icon}
                      aria-label="create pricing"
                      component="span"
                    >
                      <ControlPointIcon />
                      <Typography className={classes.typo2}>
                        ADD SLOTS
                      </Typography>
                    </IconButton>
                  </Grid>
                </Grid>
                {/* <Grid lg={6} md={12}>
                                <Paper className={classes.paper2} elevation={0}> */}
                <FieldArray name="slots">
                  {({ fields }) =>
                    fields.map((name, index) => (
                      <Grid lg={6} md={12}>
                        <Paper className={classes.paper2} elevation={0}>
                          <Grid lg={12} className={classes.grid1}>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Start Time
                              </Typography>

                              <Field
                                name={`${name}.start_time`}
                                component={package_slot_start_time}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                End Time
                              </Typography>
                              <Field
                                name={`${name}.end_time`}
                                component={package_slot_end_time}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Hourly Pricing
                              </Typography>
                              <Field
                                name={`${name}.hourly_pricing`}
                                component={package_slot_hourly_price}
                              />
                            </Grid>
                            {index !== 0 ? (
                              <span
                                onClick={() => fields.remove(index)}
                                style={{ cursor: "pointer" }}
                              >
                                ❌
                              </span>
                            ) : null}
                          </Grid>
                          <Grid lg={12} className={classes.grid1}>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Total Pricing
                              </Typography>

                              <Field
                                name={`${name}.slot_extension_pricing`}
                                component={package_slot_extension_pricing}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Discount
                              </Typography>
                              <Field
                                name={`${name}.volume_discount`}
                                component={package_slot_volume_discount}
                              />
                            </Grid>
                            <Grid lg={4}>
                              <Typography className={classes.typo3}>
                                Select Sport
                              </Typography>
                              <Field
                                name={`${name}.discount`}
                                component={package_slot_discount}
                              />
                            </Grid>
                          </Grid>
                        </Paper>
                        <Divider />
                      </Grid>
                    ))
                  }
                </FieldArray>
                <Grid
                  lg={6}
                  md={12}
                  className={(classes.grid1, classes.paddingLeft)}
                >
                  <Button
                    type="submit"
                    className={classes.button}
                    disabled={submitting ? submitting : false}
                  >
                    Save
                  </Button>
                </Grid>
                <Grid
                  className={classes.paddingTop}
                  container
                  direction="row"
                  justifyContent="flex-start"
                  alignItems="center"
                >
                  <Submit
                    handleclick={() => dispatch(setSteps(steps - 1))}
                    title="back"
                    width="110px"
                  />
                  <Submit
                    handleclick={() => history.push("/slots")}
                    title="finish"
                    width="110px"
                  />
                </Grid>
              </Grid>
            </form>
          )}
        />
      ) : null}
    </>
  );
};

export default AddEditPricingForm;
