import React from "react";
import Table from "../../common/Table2";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import EditOutlinedIcon from "@material-ui/icons/EditOutlined";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";

import Switch from "../../common/Switch";

const useStyles = makeStyles((theme) => ({
  typo3: {
    color: "#464646",
    textAlign: "left",
    fontSize: "15px",
    fontWeight: "400",
  },
}));

const columns = [
  { id: "activity", label: "Activity/Sports", minWidth: 100 },
  { id: "name", label: "Coupon Name", minWidth: 120 },
  { id: "date", label: "Dates/Days", minWidth: 100 },
  {
    id: "timing",
    label: "Timing",
    minWidth: 120,
    align: "center",
  },
  {
    id: "discount",
    label: "Discount",
    minWidth: 120,
  },
  {
    id: "handler",
    label: "Handler/Timestamp",
    minWidth: 120,
  },
  {
    id: "status",
    label: "Status",
    minWidth: 120,
  },
];

const List = (props) => {
  const { data } = props;
  const [rows, setRows] = React.useState([]);
  const classes = useStyles();

  const styleOthers = (data2) => {
    const i = data2.indexOf("::");
    if (i >= 0) {
      return (
        <div>
          <Typography className={classes.typo3}>
            {data2.substr(0, i)}
          </Typography>
          <Typography className={classes.typo3}>
            {data2.substr(i + 2)}
          </Typography>
        </div>
      );
    } else return <Typography className={classes.typo3}>{data2}</Typography>;
  };

  const styleAction = (id) => {
    return (
      <div>
        <Switch />
      </div>
    );
  };

  function createData(
    oldid,
    oldname,
    oldphone,
    oldemail,
    oldmemtype,
    oldduration,
    oldaction
  ) {
    const activity = styleOthers(oldid);
    const name = styleOthers(oldname);
    const date = styleOthers(oldphone);
    const timing = styleOthers(oldemail);
    const discount = styleOthers(oldduration);
    const handler = styleOthers(oldmemtype, 1);
    const status = styleAction(oldaction);
    return { activity, name, date, timing, discount, handler, status };
  }

  React.useEffect(() => {
    if (data && data[0]) {
      const tempData = data.map((ele) => {
        const sport = ele.sport_id + "";
        const name = ele.name;
        const date = ele.from_date + " to " + ele.to_date + " :: " + ele.days;
        const time = ele.from_time + " to " + ele.to_time;
        const discount = ele.discount_percent + "%::Limit: " + ele.amount;
        const name2 = "Amit Shah::05-Sep-21 | 06:30 am";
        const status = "1";
        return createData(sport, name, date, time, discount, name2, status);
      });
      setRows(tempData);
    }
  }, [data]);

  // const rows = [
  //   createData(
  //     "Badminton",
  //     "Diwali Offer",
  //     "1-Jan to 31-Jan_21::All Days",
  //     "4pm-6pm",
  //     "Percentage 5%::Limit: 1000",
  //     "Amit Shah::05-Sep-21 | 06:30 am",
  //     "1"
  //   ),
  // ];
  return <Table columns={columns} rows={rows} />;
};

export default List;
