import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { Divider } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import Button from "@material-ui/core/Button";

import DatePicker from "../../common/DatePicker";
import Select from "../../common/Select";
import SelectPricing from "../../common/SelectPricing";
import WeekCheckBox from "../../common/WeekCheckBox";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(3),
  },
  grid: {
    paddingTop: theme.spacing(5),
  },
  typo3: {
    color: "#484848",
    textAlign: "left",
    fontSize: "17px",
    fontWeight: "600",
    marginBottom: theme.spacing(0.5),
    marginTop: theme.spacing(1),
  },
  typo4: {
    color: "#808080",
    fontSize: "14px",
  },
  paddingRight: {
    paddingRight: theme.spacing(2),
  },
  textField: {
    width: "100%",
  },
  divider: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  button: {
      marginTop: theme.spacing(2),
      marginBottom: theme.spacing(2),
      minWidth: '140px',
      backgroundColor: '#272727',
      color: '#fff'
  }
}));

const weekData = [
  {
    id: 1,
    name: "8:00pm to 9:00pm",
  },
  {
    id: 2,
    name: "9:00pm to 10:00pm",
  },
  {
    id: 3,
    name: "10:00pm to 11:00pm",
  },
];

const sportData = [
  {
    id: 1,
    name: "Football 5-aSide",
  },
  {
    id: 2,
    name: "Hockey 6-aSide",
  },
  {
    id: 3,
    name: "Cricket 7-aSide",
  },
];

const Form = () => {
  const [formData, setFormData] = React.useState({
    sports: null,
    turf: null,
  });
  const [selectedValue, setSelectedValue] = React.useState("a");

  const handleChange = (event) => {
    setSelectedValue(event.target.value);
  };
  const classes = useStyles();
  const SetFormData = (newData) => {
    let newFormData = formData;
    if (newData.sports === "null") {
      newFormData.sports = null;
    } else {
      newData.sports ? (newFormData.sports = newData.sports) : (newFormData.date = newData.date);
    }
    setFormData(newFormData);
  };
  const getSelectValue = (value) => {
    console.log("value :>> ", value);
  };
  return (
    <Paper className={classes.paper}>
      <Grid className={classes.grid}>
        <Grid container lg={12}>
          <Grid lg={4} className={classes.paddingRight}>
            <Typography className={classes.typo3}>COUPON NAME</Typography>
            <TextField
              className={classes.textField}
              id="name"
              label="Enter coupon name"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
          <Grid lg={4}>
            <Typography className={classes.typo3} component="div" noWrap>
              Sports
            </Typography>
            <Select
              data={sportData}
              regularSelect={1}
              changeFormData={(newData) => SetFormData(newData)}
            />
          </Grid>
        </Grid>
        <Grid container lg={12}>
          <Grid lg={4} className={classes.paddingRight}>
            <Typography className={classes.typo3} component="div" noWrap>
              From
            </Typography>
            <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
          </Grid>
          <Grid lg={4}>
            <Typography className={classes.typo3} component="div" noWrap>
              To
            </Typography>
            <DatePicker fullWidth={1} changeFormData={(newData) => SetFormData(newData)} />
          </Grid>
        </Grid>
        <Divider className={classes.divider} />
        <Grid container lg={12}>
          <Grid lg={4} className={classes.paddingRight}>
            <Typography className={classes.typo3} component="div" noWrap>
              TIME
            </Typography>
            <Grid lg={4}>
              <SelectPricing
                data={weekData}
                funct="from"
                title="From"
                getSelectValue={(value) => {
                  getSelectValue(value);
                }}
              />
              <SelectPricing
                data={weekData}
                funct="to"
                title="To"
                getSelectValue={(value) => {
                  getSelectValue(value);
                }}
              />
            </Grid>
          </Grid>
          <Grid lg={4} className={classes.paddingRight}>
            <Typography className={classes.typo3} component="div" noWrap>
              DAY
            </Typography>
            <WeekCheckBox />
          </Grid>
        </Grid>
        <Divider className={classes.divider} />
        <Grid container lg={12}>
          <Grid lg={4}>
            <Typography className={classes.typo3} component="div" noWrap>
              TYPE
            </Typography>
            <Radio
              checked={selectedValue === "a"}
              onChange={handleChange}
              value="a"
              name="radio-button-demo"
              inputProps={{ "aria-label": "A" }}
              label="Cash Discount"
            />
            Cash Discount
            <Radio
              checked={selectedValue === "b"}
              onChange={handleChange}
              value="b"
              name="radio-button-demo"
              inputProps={{ "aria-label": "B" }}
              label="Percentage Discount"
            />
            Percentage Discount
            <TextField
              className={classes.textField}
              id="amount"
              label="Amount"
              defaultValue=""
              variant="outlined"
              size="small"
              type="input"
            />
          </Grid>
          <Grid lg={4}>
            <Typography className={classes.typo3} component="div" noWrap>
              PERMANENT
            </Typography>
            <Typography className={classes.typo4}>
              Yes (If yes, coupon can be used multiple times over specified Period)
            </Typography>
          </Grid>
        </Grid>
        <Divider className={classes.divider} />
        <Grid container lg={6}>
          <Typography className={classes.typo3} component="div" noWrap>
            REMARKS
          </Typography>
          <TextField
            className={classes.textField}
            id="remark"
            label="Coupon Remarks here"
            defaultValue=""
            variant="outlined"
            size="small"
            type="input"
          />
        </Grid>
        <Grid container lg={4}>
          <Typography className={classes.typo3} component="div" noWrap>
            COUPON COUNT
          </Typography>
          <TextField
            className={classes.textField}
            id="nocoupon"
            label="Number of coupons to generate"
            defaultValue=""
            variant="outlined"
            size="small"
            type="number"
          />
        </Grid>
        <Button variant="contained" className={classes.button}>Confirm</Button>
      </Grid>
    </Paper>
  );
};

export default Form;
